main {
	overflow: hidden;
	figure {
		width: 100%;
		position: relative;
		background-color: #000;

		.video {
			width: 100%;
			height: calc(100vh - 80px);

			video {
				width: 100%;
				height: 100%;
				object-fit: cover;
				filter: grayscale(0.9);
			}
		}
		.wrap {
			z-index: 5;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(0, -50%);
			text-align: center;

			h1 {
				font-size: 40px;
				margin-bottom: 50px;
				letter-spacing: 3px;
				text-shadow: 3px 2px 5px #000;
				font-family: oswald;
				color: #d5a869;
			}
			h2 {
				font-size: 80px;
				margin-bottom: 50px;
				letter-spacing: 3px;
				text-shadow: 3px 2px 5px #000;
				font-family: libre bodoni;
				color: #d5a869;
			}
			h3 {
				font-size: 40px;
				margin-bottom: 0px;
				letter-spacing: 3px;
				text-shadow: 3px 2px 5px #000;
				font-family: libere bodoni;
				color: #d5a869;
			}
		}
	}
}

//tablet
@media screen and (max-width: $tablet) {
	main {
		figure {
			.wrap {
				h1 {
					font-size: 30px;
				}
				h2 {
					font-size: 60px;
				}
				h3 {
					font-size: 30px;
				}
			}
		}
	}
}

//m.p
@media screen and (max-width: $mp) {
	main {
		figure {
			.wrap {
				h1 {
					font-size: 20px;
				}
				h2 {
					font-size: 50px;
				}
				h3 {
					font-size: 20px;
				}
			}
		}
	}
}
